@supports (display: grid) {
  @media screen and (max-width: 1000px) {
    .text {
      text-align: center;
      font-size: 60px;
      font-family: "American Typewriter", serif;
    }
  }
  @media screen and (min-width: 1000px) {
    .text {
      text-align: center;
      font-size: 100px;
      font-family: "American Typewriter", serif;
    }
  }
}
